		"desc_text": "SunTrax is situated on 475 acres and is composed of a 2.25-mile-long oval test track around a 200-acre infield. The multi-lane track will make it the only high-speed autonomous vehicle (AV) testing facility in the southeastern United States. In the infield, there will be multiple simulated transportation environments, including:An urban area to simulate intersection configurations and complex lighting, signing, and signalization conditionsA 20-acre open paved technology pad providing the ability to replicate nearly any real-world geometric configuration and perform endless test scenariosA roadway geometry track made up of complex horizontal and vertical curves, plus irregular grade changesA pick-up/drop-off area to replicate various multi-modal passenger transfers, such as airports and transit centersThe intent is to maximize the sites flexibility and variability, providing as many challenging environments as possible for the testing of connected and autonomous vehicles (CAV) and other emerging technologies. The entire site will be a connected environment for the testing of vehicle-to-infrastructure (V2I), vehicle-to-vehicle (V2V), and vehicle-to-anything (V2X) communications. SunTrax will also have a unique tolls technology testing component, with four all-electronic toll gantries and supporting infrastructure being constructed. FTE will conduct its tolls testing on the site, both on existing hardware and software systems and future generations of tolling equipment and payment systems.",
