		"desc_text": "This project is deployed with connected vehicle (CV) technologies and applications along four roads forming a trapezium surrounding the University of Florida main campus. The routes are SR 121 (SW 34th St), SR 26 (W University Ave), US 441 (SW 13th St), and SR 24 (SW Archer Rd). The goal of the project is to improve travel time reliability, safety, throughput, and traveler information. This project will also deploy and test pedestrian and bicyclist safety CV and smartphone-based applications. The project covers 27 traffic signals equipped with 27 Roadside Units. The project became operational in September 2019.",
